body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    background-color: #1f77b4;
    position: fixed;
    width: 100%;
    height: 6%;
    z-index: 100;
    color: white;
}

.Header-item {
    display: inline-block;
    margin: 0;
    box-sizing: border-box;
    width: auto;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 12px;
}

.Header-upload {
    display: inline-block;
    margin: 0;
    margin-top: 8px;
    box-sizing: border-box;
    width: auto;
    height: 50%;
    -webkit-align-items: right;
            align-items: right;
    color: blue;
    text-decoration: underline;
    cursor: pointer;
    position: absolute;
    left: 92%;
}
.GraphBox {
    background: white;
    width: 100%;
    height: 30%;
    position: relative;
    box-sizing: border-box;
    border-radius: 2px;
}

#BarGraph {
    fill: rgb(41, 203, 224);
    height: 90%;
    width: 100%;
    position: relative;
    top: 10%;
}

#BarGraphDos {
    fill: crimson;
    height: 90%;
    width: 100%;
    position: absolute;
    top: 10%;
}
.MapRec {
    width: 100%;
    height: 40%;
    margin-top: 16px;
    text-align: center;
    position: relative;
    background: white;
}

.Map {
    width: 100%;
    height: 100%;
}

.ZoomBtnGroup {
    position: relative;
    display: inline-block;
    left: 0;
    width:100%;
    height: auto;
    margin-top: 2px;
    text-align: left;
}

.ZoomBtn {
    margin-top: 0px;
}
.Table {
    width: 90%;
    height: 30%;
    display: inline-table;
    box-sizing: border-box;
    position: relative;
    text-align: center;
    border-radius: 5px;
    margin-top: 16px;
    background: white;
}
.DashSection {
    display: inline-block;
    position: relative;
    top: 0;
    height: 100%;
    vertical-align: top;
    margin: 8px;
}
.Graph {
    background: white;
    width: 100%;
    height: 30%;
    top: 1%;
    position: relative;
    box-sizing: border-box;
    border-radius: 2px;
}

.line {
    fill: none;
    stroke: #ff7f0e;
    stroke-width: 3;
}

.dot {
    fill: #ff7f0e;
}

#LineGraph {
    width: 100%;
    height: 100%;
    position: relative;
}

#LineGraph2 {
    width: 100%;
    height: 100%;
    position: relative;
}
.PieBox {
    height: 20%;
    width: 80%;
    left: 12.5%;
    position: relative;
    top: 2%;
}

#PieChart {
    height: 100%;
    width: 100%;
    display: block;
    margin: auto;
}

#PieChart2 {
    height: 100%;
    width: 100%;
    position: relative;
    display: block;
    margin: auto;
}

.toolTip {
    border: 1px solid black;
    box-shadow: 4px 8px;
    background: gray;
    color: black;
}
.FilterTop {
    font-size: 18px;
    font-weight: bold;
    color: rgb(24, 24, 24);
}

.SelectBox {
    margin: 0px 8px;
}
.Dashboard {
    background-color: rgb(228, 228, 228);
    color: rgb(12,12,12);
    display: inline-block;
    width: 100%;
    height: 800px;
    box-sizing: border-box;
    border-radius: 2px;
    position: relative;
    left: 0;
    margin-top: 2.9%;
    text-align: center;
}

.pieSection {
    width: 100%;
    height: 30%;
    margin: 32px;
    position: relative;
}
.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.5);
}
.Modal {
    position: fixed;
    width: 70%;
    height: 60%;
    z-index: 500;
    background-color: white;
    width: 50%;
    border: 1px 1px 1px black;
    border-radius: 5px;
    padding: 16px;
    left: 25%;
    top: 30%;
    box-sizing: border-box;
    -webkit-transition: all 0.6s ease-in-out .2s;
    transition: all 0.6s ease-in-out .2s;
}
.FileDropBtn {
    text-align: center;
    background: brown;
    position: fixed;
    left: 50%;
    top:90%;
}
.App {

}
